<template>

<div class="map_wrap">
  <div id="map" ref="map"></div>
  
<ul id="category">
  <li id="BK9" data-order="0"> 
      <span class="category_bg bank"></span>
      
  </li>       
  <li id="MT1" data-order="1"> 
      <span class="category_bg mart"></span>
      
  </li>  
  <li id="PM9" data-order="2"> 
      <span class="category_bg pharmacy"></span>
      
  </li>  
  <li id="OL7" data-order="3"> 
      <span class="category_bg oil"></span>
      
  </li>  
  <li id="CE7" data-order="4"> 
      <span class="category_bg cafe"></span>
      
  </li>  
  <li id="CS2" data-order="5"> 
      <span class="category_bg store"></span>
      
  </li>      
</ul>
  <!-- <p id="inputtag">
    <input type="checkbox" id="chkUseDistrict" @click="setOverlayMapTypeId" /><label for="chkUseDistrict"> </label>
    <input type="checkbox" id="chkTerrain" @click="setOverlayMapTypeId" /><label for="chkTerrain"> </label>
    <input type="checkbox" id="chkTraffic" @click="setOverlayMapTypeId" /><label for="chkTraffic"> </label>
    <input type="checkbox" id="chkBicycle" @click="setOverlayMapTypeId" /><label for="chkBicycle"> </label>
  </p> -->

  <v-speed-dial
    v-model="mapmenu"
    direction="left"
    bottom
    right
    class="juk-normapmainmenu"
  >
    <template v-slot:activator>
      <v-btn
        v-model="mapmenu"
        width="80px"
        height="80px"
        color="blue"
        dark
        fab
      >
        <v-icon x-large v-if="mapmenu">mdi-close</v-icon>
        <v-icon x-large v-else>mdi-apps</v-icon>
      </v-btn>
    </template>
      <v-btn
        fab
        to="/"
      >
        <v-icon medium>home</v-icon>
      </v-btn>
      <v-btn
        fab
        to="/map/apart"
      >
        <v-icon medium>apartment</v-icon>
      </v-btn>
  </v-speed-dial>

  <!-- (, , , ) -->
  <v-speed-dial
    v-model="plusmenu"
    direction="left"
    bottom
    right
    class="juk-normapplusmenu"
  >
    <template v-slot:activator>
      <v-btn
        v-model="plusmenu"
        width="80px"
        height="80px"
        color="blue"
        dark
        fab
      >
        <v-icon x-large v-if="plusmenu">mdi-close</v-icon>
        <v-icon x-large v-else>mdi-layers</v-icon>
      </v-btn>
    </template>
      <!--  -->
      <v-btn
        v-model="mapDistrict"
        fab
        @click="setOverlayMapTypeId('district')"
      >
        <v-icon>
          {{ mapDistrict ? 'mdi-view-dashboard' : 'mdi-view-dashboard-outline' }}
        </v-icon>
      </v-btn>

      <!--  -->
      <v-btn
        v-model="terrain"
        fab
        @click="setOverlayMapTypeId('terrain')"
      >
        <v-icon>
          {{ terrain ? 'mdi-map' : 'mdi-map-outline' }}
        </v-icon>
      </v-btn>

      <!--  -->
      <v-btn
        v-model="traffic"
        fab
        @click="setOverlayMapTypeId('traffic')"
      >
        <v-icon>
          {{ traffic ? 'mdi-car' : 'mdi-car-off' }}
        </v-icon>
      </v-btn>

      <!--  -->
      <v-btn
        v-model="bicycle"
        fab
        @click="setOverlayMapTypeId('bicycle')"
      >
        <v-icon>
          {{ bicycle ? 'mdi-bike' : 'mdi-bicycle' }}
        </v-icon>
      </v-btn>
  </v-speed-dial>
</div>

</template>

<script>
import categoryMap from "@/assets/js/map.js";

export default {

  mounted() {
    let container = this.$refs.map;

    this.$root.$on('centerOne', aptSeq => {
      let target = this.markerList.find(e => e.data.NORSALES_NUM === aptSeq);

      if (target) {
        this.markerList.forEach(e => {
          e.setImage(this.markerImage.normal);
        });
        target.setImage(this.markerImage.point);
      }
    });

    this.$parent.$on('refreshNor', (e) => {
      this.refresh(e);
    });

    this.$parent.$on('changedEtcFilter', (filter) => {
      this.filter = filter;
      function between(value, arr) {
        return +value >= arr[0] && +value <= arr[1];
      }
      console.log(filter);
      for (let marker of this.markerList) {
        let e = marker.data;
        if (e.BUILD_TYPE == filter.method && (e.BUILD_TYPE !== '' || between(e.DEPOSIT, filter.deposit)) && between(e.AREA, filter.area) && between(e.PRICE, filter.price)) {
          marker.setImage(this.markerImage.booking);
        } else {
          marker.setImage(this.markerImage.normal);
        }
      }
    });

    (async () => {
      navigator.geolocation.getCurrentPosition(position => {
        let options = {
          center: new kakao.maps.LatLng(position.coords.latitude, position.coords.longitude),
          level: 5,
        };
    
        this.map = new kakao.maps.Map(container, options);
        
        // 
        var centerXY = this.map.getCenter(); 

        // -   
        var geocoder = new kakao.maps.services.Geocoder();

        //      
        this.geocoderAddr = geocoder.coord2Address(centerXY.getLng(), centerXY.getLat(), async (result, status) => {
        
          if (status !== kakao.maps.services.Status.OK) return;

          let rankList = (await axios({
            url: `${this.serverLocation}/getPopulOneRank?si=${result[0].address.region_1depth_name}&gu=${result[0].address.region_2depth_name}&dong=${result[0].address.region_3depth_name}`,
            method: 'GET',
          })).data;

          this.$root.$emit('oneRankList', rankList);
        });

        categoryMap(this.map);
        
        //           
        var mapTypeControl = new kakao.maps.MapTypeControl();

        //     
        // kakao.maps.ControlPosition     TOPRIGHT   
        this.map.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT);

        //          
        var zoomControl = new kakao.maps.ZoomControl();
        this.map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);

        this.cluster = new kakao.maps.MarkerClusterer({
            map: this.map,
            averageCenter: true,
            minLevel: 6
        });

        this.refresh();
        kakao.maps.event.addListener(this.map, 'dragend', this.refresh);

        //   
        kakao.maps.event.addListener(this.map, 'dragend', () => {

          var centerXY = this.map.getCenter(); 
          // -   
          var geocoder = new kakao.maps.services.Geocoder();

          //      
          this.geocoderAddr = geocoder.coord2Address(centerXY.getLng(), centerXY.getLat(), async (result, status) => {

            if (status !== kakao.maps.services.Status.OK) return;
            
            let rankList = (await axios({
              url: `${this.serverLocation}/getPopulOneRank?si=${result[0].address.region_1depth_name}&gu=${result[0].address.region_2depth_name}&dong=${result[0].address.region_3depth_name}`,
              method: 'GET',
            })).data;

            this.$root.$emit('selectdong', result[0].address.region_3depth_name)
            this.$root.$emit('oneRankList', rankList);

          });
        });
        kakao.maps.event.addListener(this.map, 'zoom_changed', this.refresh);
      }, failur => {
        console.log('only secure origins are alllowed!');
        let options = {
          center: new kakao.maps.LatLng(36.3248296, 127.4179048),
          level: 5,
        };
    
        this.map = new kakao.maps.Map(container, options);
        
        // 
        categoryMap(this.map);
        
        //           
        var mapTypeControl = new kakao.maps.MapTypeControl();

        //     
        // kakao.maps.ControlPosition     TOPRIGHT   
        this.map.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT);

        //          
        var zoomControl = new kakao.maps.ZoomControl();
        this.map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);

        this.cluster = new kakao.maps.MarkerClusterer({
            map: this.map,
            averageCenter: true,
            minLevel: 6
        });

        this.refresh();
        kakao.maps.event.addListener(this.map, 'dragend', this.refresh);
        kakao.maps.event.addListener(this.map, 'zoom_changed', this.refresh);
      });
    })();
  },
  data() {
    return {
      map: null,
      cluster: null,
      currentTypeId: '',
      markerImage: {
        normal: new kakao.maps.MarkerImage(`//192.168.0.121:9000/api/file/icon/oneroom_off.png`, new kakao.maps.Size(55, 42), new kakao.maps.Point(0, 0)),
        booking: new kakao.maps.MarkerImage(`//192.168.0.121:9000/api/file/icon/oneroom_on.png`, new kakao.maps.Size(55, 42), new kakao.maps.Point(0, 0)),
        point: new kakao.maps.MarkerImage(`//192.168.0.121:9000/api/file/icon/oneroom_point.png`, new kakao.maps.Size(55, 42), new kakao.maps.Point(0, 0)),
      },
      filter: {},
      markerList: [],
      overlayList: [],
      mapmenu: false,
      plusmenu: false,
      mapDistrict: false,
      terrain: false,
      traffic: false,
      bicycle: false,
    };
  },
  methods: {
    refresh({ arr, filter } = {}) {
      if (filter) {
        this.filter = filter;
      } else {
        filter = this.filter;
      }

      if (this.map.getLevel() > 5) return;
      
      this.cluster.clear();
      let center = this.map.getCenter();
      let position = {
        lng: center.getLng(),
        lat: center.getLat(),
      };

      if (arr) {
        position.lat = arr.LAT;
        position.lng = arr.LNG;
        this.map.setCenter(new kakao.maps.LatLng(arr.LAT, arr.LNG));
      }
      
      (async () => {
        let addressList = (await axios({
          url: `${this.serverLocation}/c2n`,
          method: 'GET',
        })).data.map(e => ({
          ...e,
          DEPOSIT: (e.DEPOSIT / 10000).toFixed(0),
          PRICE: (e.PRICE / 10000).toFixed(0),
        }));

        
        let markers = [];

        function between(value, _arr) {
          _arr = _arr || [0, 999999999999];
          return +value >= _arr[0] && +value <= _arr[1];
        }

        this.overlayList.forEach(e => {
          e.setMap(null);
        });
        this.overlayList = [];

        for (let e of addressList) {
          let position = new kakao.maps.LatLng(e.SALES_LAT, e.SALES_LNG);
          const marker = new kakao.maps.Marker({ position });
          const overlay = new kakao.maps.CustomOverlay({
            position,
            content: `<div class="juk-priceBox1">${e.DEPOSIT} <br> /${e.PRICE}</div>`,
          });
          overlay.setMap(this.map);
          this.overlayList = [...this.overlayList, overlay];
          marker.data = e;

        
          let bookingCondition = e.BUILD_TYPE;
          if (bookingCondition && between(e.AREA, filter.area)) {
            if (e.BUILD_TYPE === '') {
              bookingCondition = between(e.DEPOSIT, filter.price) && between(e.PRICE, filter.deposit);
            } else {
              bookingCondition = between(e.PRICE, filter.price);
            }
          }
          if (bookingCondition) {
            marker.setImage(this.markerImage.booking);
          } else {
            marker.setImage(this.markerImage.normal);
          }

          kakao.maps.event.addListener(marker, 'click', () => {
            this.$root.$emit('selectNor', e);
          });

          kakao.maps.event.addListener(marker, 'mouseover', () => {
            marker.setOpacity(0.75);
          });

          kakao.maps.event.addListener(marker, 'mouseout', () => {
            marker.setOpacity(1);
          });

          // marker.setMap(this.map);
          markers.push(marker);
        }
        this.markerList = markers;
        this.cluster.addMarkers(markers);
      })();
    }, //   
    setOverlayMapTypeId(type) {
      if(type == 'district'){
        this.mapDistrict = !this.mapDistrict;
      }else if(type == 'terrain'){
        this.terrain = !this.terrain;
      }else if(type == 'traffic'){
        this.traffic = !this.traffic;
      }else if(type == 'bicycle'){
        this.bicycle = !this.bicycle;
      }

      var mapTypes = {
          terrain : kakao.maps.MapTypeId.TERRAIN,    
          traffic :  kakao.maps.MapTypeId.TRAFFIC,
          bicycle : kakao.maps.MapTypeId.BICYCLE,
          useDistrict : kakao.maps.MapTypeId.USE_DISTRICT
      };

      var chkTerrain = document.getElementById('chkTerrain'),  
          chkTraffic = document.getElementById('chkTraffic'),
          chkBicycle = document.getElementById('chkBicycle'),
          chkUseDistrict = document.getElementById('chkUseDistrict');
      
      //   
      for (var type in mapTypes) {
          this.map.removeOverlayMapTypeId(mapTypes[type]);    
      }

      //       
      if (this.mapDistrict) {
          this.map.addOverlayMapTypeId(mapTypes.useDistrict);    
      }
      
      //       
      if (this.terrain) {
          this.map.addOverlayMapTypeId(mapTypes.terrain);    
      }
      
      //       
      if (this.traffic) {
          this.map.addOverlayMapTypeId(mapTypes.traffic);    
      }
      
      //       
      if (this.bicycle) {
          this.map.addOverlayMapTypeId(mapTypes.bicycle);    
      }
      
    },
    // ...{ setMapType, zoomIn, zoomOut },
  }

}
</script>

<style>
#map {
  position: fixed;
  top: 0;
  height: 0;
  width: calc(100% - 450px);
  height: 100vh;
}

/*  */
.map_wrap, .map_wrap * {margin:0; padding:0;font-size:12px;}
.map_wrap {position:relative;width:100%;height:100vh;}
#category {position:absolute;top:5px;right:580px;border-radius: 5px; border:1px solid #909090;box-shadow: 0 1px 1px rgba(0, 0, 0, 0.4);background: #fff;overflow: hidden;z-index: 2;}
#category {padding-left:5px;}
#category li {float:left;list-style: none;width:50px;border-right:1px solid #acacac;padding:10px 0px;text-align: center; cursor: pointer;}
#category li.on {background: #eee;}
#category li:hover {background: #ffe6e6;border-left:1px solid #acacac;margin-left: -1px;}
#category li:last-child{margin-right:0;border-right:0;}
#category li span {display: block;margin:0 auto 3px;width:27px;height: 28px;}
#category li .category_bg {background:url(http://t1.daumcdn.net/localimg/localimages/07/mapapidoc/places_category.png) no-repeat;}
#category li .bank {background-position: -10px 0;}
#category li .mart {background-position: -10px -36px;}
#category li .pharmacy {background-position: -10px -72px;}
#category li .oil {background-position: -10px -108px;}
#category li .cafe {background-position: -10px -144px;}
#category li .store {background-position: -10px -180px;}
#category li.on .category_bg {background-position-x:-46px;}

/*   css */
.placeinfo_wrap {position:absolute;bottom:28px;left:-150px;width:300px;}
.placeinfo {position:relative;width:100%;border-radius:6px;border: 1px solid #ccc;border-bottom:2px solid #ddd;padding-bottom: 10px;background: #fff;}
.placeinfo:nth-of-type(n) {border:0; box-shadow:0px 1px 2px #888;}
.placeinfo_wrap .after {content:'';position:relative;margin-left:-12px;left:50%;width:22px;height:12px;background:url('http://t1.daumcdn.net/localimg/localimages/07/mapapidoc/vertex_white.png')}
.placeinfo a, .placeinfo a:hover, .placeinfo a:active{color:#fff;text-decoration: none;}
.placeinfo a, .placeinfo span {display: block;text-overflow: ellipsis;overflow: hidden;white-space: nowrap;}
.placeinfo span {margin:5px 5px 0 5px;cursor: default;font-size:13px;}
.placeinfo .title {font-weight: bold; font-size:14px;border-radius: 6px 6px 0 0;margin: -1px -1px 0 -1px;padding:10px; color: #fff;background: #d95050;background: #d95050 url(http://t1.daumcdn.net/localimg/localimages/07/mapapidoc/arrow_white.png) no-repeat right 14px center;}
.placeinfo .tel {color:#0f7833;}
.placeinfo .jibun {color:#999;font-size:11px;margin-top:0;}

#inputtag{
  width: 420px;
  height: 30px;
  position: relative;
  margin-left: 400px;
  margin-top: 23px;
  padding-left: 10px; 
}

#inputtag label {
  font-size: 1.4em;
  font-weight: bold;
  user-select: none;
}

/*  */
.juk-priceBox1 {
  width: 40px;
  height: 41px;
  background-color: rgba(247, 183, 6, 0.8);
  color: #FFF;
  border-radius: 10px;
  position: absolute;
  top: -47px;
  left: 10px;
  text-align: center;
  padding-top: 3px;
  box-shadow: 1px 1px 2px #888;
}

.juk-priceBox1::after {
  content: ' ';
  transform: rotate(45deg);
  width:9px;
  height: 10px;
  position: absolute;
  top: 35px;
  left: 10px;
  background-color: rgba(247, 183, 6, 0.6);
  box-shadow: 1px 1px 2px #888;
}

.juk-normapmainmenu {
  position: absolute;
  bottom: 40px;
  right: 500px;
}

.juk-normapplusmenu {
  position: absolute;
  bottom: 140px;
  right: 500px;
}
</style>


